import React, { PureComponent } from "react";
import { graphql } from "gatsby";
import * as Common from "../common";
import * as Func from "../libs/common/func";
import * as Organisms from "../components/organisms";
import { css } from "@emotion/react";
import { documentToReactComponents } from "@contentful/rich-text-react-renderer";
import * as Libs from "libs";

export class Tag extends PureComponent {
	contCss = () => css`
		${Func.getPcSpVwValue("margin-top", 350, 275, true)};
	`;

	constructor(props) {
		super(props);

		this.counter = 0;
	}
	componentDidMount() {
		Libs.Common.Config.excuteTransition();
	}
	render() {
		const { data, pageContext } = this.props;
		let tags = data.allWordpressPost.edges;
		const currentSlug = pageContext.slug;
		const type = pageContext.type;
		tags = tags.map(({ node }) => {
			return node;
		});
		const items = tags;
		return (
			<>
				<Libs.Common.SEO
					title={tags.name}
					lang={tags.node_locale}
					link={tags.slug}
					description={
						tags.description
							? documentToReactComponents(tags.description.json)[0].props
									.children[0]
							: ""
					}
				/>
				<section
					css={this.contCss}
					className={"project-container init-animat-cont"}
					data-current={pageContext.slug}
					data-currentname={pageContext.name}
					data-categoryname={type}
					data-categoryslug={type}
					data-type={"category"}
				>
					<Organisms.CategoryPage
						description={tags.description ? tags.description.json : null}
						type={"category"}
						items={items}
						slug={currentSlug}
					/>
				</section>
			</>
		);
	}
}

export default Tag;

export const pageQuery = graphql`
	query tagQuery($slug: String!) {
		allWordpressPost(filter: { tags: { elemMatch: { slug: { eq: $slug } } } }) {
			edges {
				node {
					slug
					title
					featured_media {
						localFile {
							childImageSharp {
								fluid {
									src
								}
							}
						}
					}
					categories {
						name
						slug
					}
					tags {
						name
						slug
						description
					}
				}
			}
		}
	}
`;
